.inputs {


  span {
    margin-right: 10px;
  }

  input {
    width: 38%;
    height: 45px;
    border: 1px solid #F3F3F3;
    border-radius: 10px;
    margin-right: 20px;
    padding: 0 10px;

    &:hover {
      box-shadow: -2px 3px 14px 2px rgba(105, 151, 187, 0.76);
    }
  }
}

